.wrapper {
  border: 1px solid var(--color-brd);
  border-radius: var(--border-rad);
  margin-top: 50px;
}

.labelWrapper {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  padding: 12px;
  border-bottom: 1px solid var(--color-brd);
}

.labelWrapper:hover .icon {
  opacity: 1;
}

.title {
  font-weight: 600;
  font-size: 2rem;
  line-height: 2.8rem;
}

.icon {
  opacity: 0.7;
  transition: opacity 0.15s ease-in-out, transform .25s ease-in-out;
}

.icon:hover {
  opacity: 1;
}

.content {
  display: flex;
  flex-shrink: 0;
  position: relative;
}

.left {
  width: 250px;
  border-right: 1px solid #525356;
  flex-shrink: 0;
  position: relative;
}

.right {
  width: 100%;
  padding: 16px;
}

.itemsWrapper {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}

.noShow {
  overflow: hidden;
  transition: all 0.3s ease;
}

.rotate {
  transform: rotate(180deg);
}

.open.labelWrapper {
  border: none;
}

@media (max-width: 710px) {
  .left {
    width: 40px;
    background-color: transparent;
  }
}