.button {
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 5rem;
  width: 100%;
  height: 5rem;
  padding: 0 2.4rem;
  border-radius: var(--border-rad);
  color: var(--color-text-primary);
  border: none;
  cursor: pointer;
  transition: .15s ease-in-out;
  text-overflow: ellipsis;
}

.overflow {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  display: inline-block;
  width: 100%;
}

.primary {
  background: var(--color-btn-primary);
}
.primary:hover {
  background: var(--color-btn-primary-hover)
}

.outline {
  background: none;
  border: 1px solid var(--color-btn-primary);
  color: var(--color-btn-primary);
  font-size: 1.5rem;
  line-height: 4.8rem;
  width: 100%;
  padding: 0 2.4rem;
  height: 4.8rem;
}
.outline:hover {
  background: var(--color-btn-primary);
  color: var(--color-text-primary);
}

.primary.sm {
  font-size: 1.5rem;
  line-height: 4.8rem;
  height: 4.8rem;
}

.outline.sm {
  font-size: 1.4rem;
  line-height: 4.6rem;
  height: 4.6rem;
}