@font-face {
  font-family: 'Inter';
  font-weight: 300;
  src: local('Inter'), url(../assets/fonts/Inter/Inter-Light.ttf) format('opentype');
}

@font-face {
  font-family: 'Inter';
  font-weight: 400;
  src: local('Inter'), url(../assets/fonts/Inter/Inter-Regular.ttf) format('opentype');
}

@font-face {
  font-family: 'Inter';
  font-weight: 500;
  src: local('Inter'), url(../assets/fonts/Inter/Inter-Medium.ttf) format('opentype');
}

@font-face {
  font-family: 'Inter';
  font-weight: 600;
  src: local('Inter'), url(../assets/fonts/Inter/Inter-SemiBold.ttf) format('opentype');
}

@font-face {
  font-family: 'Inter';
  font-weight: 700;
  src: local('Inter'), url(../assets/fonts/Inter/Inter-Bold.ttf) format('opentype');
}


html {
  scroll-behavior: smooth;
  font-size: 10px;
}

body {
  min-height: 100vh;
  overflow-y: scroll;
  font-family: 'Inter', 'Arial', sans-serif;
  color: var(--color-text-primary);
  text-rendering: optimizeSpeed;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-bcg);
}

.ReactCollapse--collapse {
  transition: height .25s ease-in-out;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #27282C;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}