.wrapper {
  background: var(--color-secondary);
  border: 1px solid var(--color-border);
  border-radius: var( --border-rad);
}

.labelWrapper {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  padding: 12px;
  border-bottom: 1px solid var(--color-border);
}
.labelWrapper:hover .icon {
  opacity: 1;
}

.title {
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 2.4rem;
}

.buttonsWrapper {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 12px;
}

.open {
  border: none;
}

.icon {
  opacity: 0.7;
  transition: opacity 0.15s ease-in-out, transform .25s ease-in-out;
}
.icon:hover {
  opacity: 1;
}

.rotate {
  transform: rotate(180deg);
}

.sm {
  font-size: 1.5rem;
}

