.modal {
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  position: absolute;
  background-color: #363636;
  border-radius: 6px;
  box-sizing: border-box;
  padding: 1rem 0;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.50);
}

.closeButton {
  position: absolute;
  right: -35px;
  top: -5px;
  cursor: pointer;
  opacity: 0.7;
  transition: .15s ease-in-out;
}
.closeButton:hover {
  opacity: 1;
}

.modalText {
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 2.2rem;
  padding: 0 1rem;
}

.margin {
  margin-top: 15px;
}

.buttonWrapper {
  padding: 1rem 1rem 0 1rem;
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 10px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  margin-top: 15px;
}

.cancelButton {
  background: #2C2B2B;
  border-radius: 6px;
  padding: 8px 12px;
  border: none;
  color: #fff;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 19px;
  cursor: pointer;
  transition: 0.15s ease;
}

.downloadButton {
  background: #05884B;
  border-radius: 6px;
  padding: 8px 12px;
  border: none;
  color: #fff;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 19px;
  cursor: pointer;
  transition: 0.15s ease;
}

.cancelButton:hover { 
  background-color: #202020;
}

.downloadButton:hover { 
  background-color: #056739;
}

@media (max-width: 760px) {
  .modal {
    width: calc(100vw - 40px);
  }
  .closeButton {
    right: -6px;
    top: -33px;
  }
}
