@import '../../styles/consts.css';

.title {
  font-size: 3rem;
  line-height: 3.6rem;
  font-weight: 600;
}

.versionNumber {
  color: var(--color-accent);
}

.contentWrapper {
  display: flex;
  margin-top: 30px;
  gap: 16px;
}

.contentLeft {
  max-width: 420px;
  min-width: 330px;
  flex-shrink: 0;
}

.otherPlatformsWrapper {
  margin-top: 15px;
}

.required {
  text-align: center;
  font-weight: 400;
  font-size: 1.3rem;
  line-height: 1.3rem;
  margin-top: 6px;
  color: var(--color-text-secondary);
}

@media (max-width: 768px) {
  .contentWrapper {
    flex-direction: column;
  }

  .contentLeft {
    width: 100%;
    min-width: auto;
    max-width: none;
  }
}

@media (max-width: 600px) {
  .showBr {
    display: none;
  }
}

@media (max-width: 540px) {
  .title {
    font-size: 2.2rem;
    line-height: 3rem;
  }
}