.notes {
  padding: 12px 16px;
  border: 1px solid var(--color-border);
  border-radius: var(--border-rad);
  position: relative;
  overflow: hidden;
  max-height: 200px;
}

.notes.open {
  max-height: unset;
}

.heading {
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 2.4rem;
}

.list {
  margin-left: 20px;
  margin-top: 2px;
}
.list li {
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 2.5rem;
  color: rgba(255, 255, 255, 0.7);
}

.more {
  position: absolute;
  right: 10px;
  bottom: 5px;
  height: 3.2rem;
  width: 14rem;
  font-weight: 600;
  font-size: 1.3rem;
  line-height: 3.2rem;
  color: var(--color-accent);
  text-align: right;
  cursor: pointer;
  background: linear-gradient(270deg, #27282C 57%, rgba(39, 40, 44, 0) 100%);
  opacity: 1;
}

.more:hover {
  text-decoration: underline;
}