.wrapper {
  width: 100%;
}

.topWrapper {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: space-between;
}

.title {
  font-size: 1.8rem;
  line-height: 2.4rem;
  font-weight: 500;
}

.date {
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: var(--color-text-secondary);
}

.content {
  margin-top: 25px;
  display: flex;
  gap: 16px;
  flex-shrink: 0;
}

.contentLeft {
  min-width: 28rem;
  max-width: 35rem;
  flex-shrink: 0;
  position: relative;
}

.marginTop {
  margin-top: 1rem;
}

@media (max-width: 1000px) {
  .content {
    flex-direction: column;
  }

  .contentLeft {
    width: 100%;
    max-width: none;
    min-width: auto;
  }
}