.wrapper {
  margin: 0 auto;
  max-width: 1200px;
  padding: 30px 20px 20px 20px;
}

.logo {
  width: 18rem;
  margin: 0 auto;
  display: flex;
  justify-content: center;
}

.title {
  font-size: 3.4rem;
  line-height: 4rem;
  font-weight: 500;
  color: #fff;
  margin-top: 30px;
  text-align: center;
}

.subTitle {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 2.3rem;
  text-align: center;
  margin-top: 20px;
}

.downloadLink {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 2.3rem;
  text-decoration-line: underline;
  color: #64DC96;
  cursor: pointer;
}

.steps {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 60px;
}

@media (max-width: 940px) {
  .steps {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 620px) {
  .title {
    text-align: center;
    font-size: 2.5rem;
    line-height: 3rem;
  }
}