.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: sticky;
  top: 16px;
  margin-top: 16px;
}

.heading {
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  display: flex;
  align-items: center;
  color: #fff;
  padding: 0 16px;
  margin-bottom: 15px;
  letter-spacing: 0.5px;
}

.item {
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0 16px;
}

.itemText {
  color: #fff;
  text-decoration: none;
  font-weight: 400;
  font-size: 16px;
  width: 75px;
  line-height: 24px;
  cursor: pointer;
}

.itemText:hover {
  color: #21a659;
}

.label {
  font-weight: 500;
  font-size: 13px;
  line-height: 24px;
  height: 24px;
  color: #fff;
  padding: 0 12px;
  text-transform: uppercase;
  border-radius: 50px;
  white-space: nowrap;
}

.labelYourVersion {
  background-color: #05884A;
}

.burgerMenuBtn {
  display: none
}

.overlay {
  width: calc(100vw - 42px);
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  position: absolute;
  z-index: 10;
  top: 0;
  border-radius: 0 0 0.8rem 0.8rem;
}

@media (max-width: 710px) {
  .burgerMenuBtn {
    display: block;
    position: sticky;
    top: 0px;
    z-index: 30;
    margin-left: 5px;
    width: 30px;
    opacity: 0.7;
  }

  .burgerMenu {
    padding-top: 35px;
    position: absolute;
    top: 0;
    z-index: 20;
    height: calc(100%);
    transition: .5s ease;
    background-color: rgba(36, 35, 35, 0.9);
    width: 0;
    margin-top: 0;
    border-radius: 0 0 0 1rem;
  }

  .open.burgerMenu {
    left: 0;
    width: 250px;
  }

  .sticky {
    position: sticky;
    top: 40px;
    transition: 0.8s;
    opacity: 0;
    margin-top: 10px;
  }

  .open.burgerMenuBtn {
    opacity: 1;
  }

  .open .sticky {
    opacity: 1;
  }

  .open .heading,
  .open .item {
    padding: 0 12px;
  }
}