.wrapper {
  width: 33%;
  height: 430px;
  box-sizing: border-box;
  padding: 16px;
  background-color: #212225;
  border-radius: 0.8rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.label {
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1.8rem;
}

.title {
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2rem;
  margin-top: 20px;
}

.instruction {
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 2.4rem;
  margin-top: 15px;
}

@media (max-width: 940px) {
  .wrapper {
    width: auto;
    max-width: 400px;
    height: 500px;
  }
}

@media (max-width: 400px) {
  .wrapper {
    height: 450px;
  }
}